<template>
    <div id="board">
        <h1>Registrarse</h1>
        <div id="msgRegister">{{msg_register}}</div>
        <form id="formRegister" @click="msgDisplay">
            
            <input id="f_name" type="text" name="first_name" placeholder="Nombre">
            <input id="f_username" type="text" name="username" placeholder="Usuario">
            <input id="f_email" type="email" name="email" placeholder="Correo electrónico">
            <input id="f_passwd" type="password" name="password" placeholder="Contraseña">
        
            <button type="button" class="btn-send btn btn-primary mt-4" @click="createUser">Crear Cuenta</button>

        </form>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'MyRegister',
    data(){
        return {
            msg_register: "",
        }
    },
    mounted(){
        this.msgDisplay();
    },
    methods: {
        createUser() {

            const registerData = new FormData();
            const form_inputs = ["f_username","f_passwd","f_email","f_name"];
            form_inputs.forEach(id => {
                const form = document.getElementById(id);
                registerData.append(form.name,form.value);
            })
            
            axios
                .post("/api/registrarse/createUser/",registerData,{
                    headers: {"Content-Type": "multipart/form-data",},
                })
                .then(response =>{
                    this.msg = response.data
                    if(this.msg.Creation){
                        this.keepSession(response.data.id);
                        location.href = "/";
                    }else{
                        this.msg_register = "Error: Usuario ya existente";
                        document.getElementById("msgRegister").style.backgroundColor="#DBE2EF";
                        document.getElementById("msgRegister").style.display="block";
                    }

                })
                .catch(error => {
                    console.log(error)
                })
            
        },
        msgDisplay(){
            document.getElementById("msgRegister").style.display="none";
        },
        keepSession(id){
            localStorage.setItem("user_session",id);
        },
    }
}
</script>

<style scoped>
#board{
    height: 600px;
    background-color: #F2F5F9;
    padding-top: 5rem;
    padding-bottom: 5rem;
    text-align: center;
}

#msgRegister{
    background-color: #DBE2EF;
    width: 300px;
    padding: 0.6rem 1.2rem 0.5rem 1.2rem;
    margin: 20px auto;
    display: none;
}

form{
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;
}

input,button{
    width: 300px;
    padding: 0.5rem 1rem 0.5rem 1rem;
}

input{
    width: 320px;
    margin-top: 10px;
}

button{
    margin-top: 0.5rem;
    width: 330px;
    color: white;
    background-color: #111729;
    cursor: pointer;
}

h1{
    padding-top: 5rem;
    margin-bottom: 2rem;
}

h5{
    font-weight: bolder;
}

a{
    color: #2c3e50;
    text-decoration: none;
}

.btn-send{
    background-color: #03506F !important;
    border-color: #03506F !important;
}

.btn-send:hover{
    background-color: #0f87b7 !important;
    border-color: #0f87b7 !important;
}

</style>
